      <div class="account-location">{{ companyInfo.bank }}</div>
      <!--查询按钮-->
      <div class="btn-search" @click="active = 2"></div>
      <div class="btn2-search" @click="active = 3"></div>
      <div class="btn3-search" @click="active = 4"></div>
      <div class="btn4-search" @click="active = 5"></div>
