      <div class="account-search-date">{{ companyInfo.openDate }}</div>
      <!--返回按钮-->
      <el-button type="primary" @click="active = 1" style="margin-left: 540px;background-color: #1f93d5;border-color: #1f93d5;margin-top: 25px;">返   回</el-button>
      <!-- <div class="btn-back" @click="active = 1">返回</div> -->
      <!--关闭按钮 去登陆后的index-->
      <!-- <div class="btn-close" @click="$router.push('/main/index')"></div> -->
