    </div>

    <div v-else-if="active === 5" class="position-relative">
      <img src="../../assets/historyBalanceQuery.png" alt="">
      <el-button type="primary" @click="goToHistoryDetails" class="historyYes"></el-button>
      <!--返回按钮-->
      <el-button type="primary" @click="active = 1" class="historyNo"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AccountYue',
  data () {
    return {
      active: 1
    }
  },
  computed: {
    companyInfo () {
      return this.$store.state.companyInfo
    }
  },
  created () {
    if (JSON.stringify(this.$route.query) !== '{}' && this.$route.query !== undefined && this.$route.query !== 'undefined' && this.$route.query !== '') {
      this.active = +this.$route.query.active
    }
  },
  methods: {
    goToDetails () {
      this.$router.push('/main/account/account-mingxi')
    },
    goToHistoryDetails () {
      this.$router.push('/main/account/account-history')
    }
  }
}
</script>

